#notfound {
   position: relative;
   height: 100vh;
 }
 
 #notfound .notfound {
   position: absolute;
   left: 50%;
   top: 50%;
   -webkit-transform: translate(-50%, -50%);
   -ms-transform: translate(-50%, -50%);
   transform: translate(-50%, -50%);
 }
 
 .notfound {
   max-width: 920px;
   width: 100%;
   line-height: 1.4;
   text-align: center;
   padding-left: 15px;
   padding-right: 15px;
 }
 
 .notfound .notfound-404 {
   position: absolute;
   height: 100px;
   top: 0;
   left: 50%;
   -webkit-transform: translate(-50%);
   -ms-transform: translateX(-50%);
   transform: translateX(-50%);
   z-index: -1;
 }
 
 .notfound .notfound-404 h1 {
   color: rgb(250, 227, 227);
   font-weight: 900;
   font-size: 276px;
   margin: 0px;
   position: absolute;
   left: 50%;
   top: 50%;
   -webkit-transform: translate(-50%, 50%);
   -ms-transform: translate(-50%, -50%);
   transform: translate(-50%, -50%);
 }
 
 .notfound h2 {
   font-size: 56px;
   color: black;
   font-weight: 900;
   text-transform: uppercase;
   margin: 0px;
 }
 
 .notfound p {
   font-size: 15px;
   color: rgb(248, 165, 165);
   font-weight: 400;
   text-transform: uppercase;
   margin-top: 15px;
 }
 
 .notfound a {
   font-size: 14px;
   text-decoration: none;
   text-transform: uppercase;
   background: rgb(198, 198, 235);
   display: inline-block;
   padding: 16px 38px;
   border: 2px solid transparent;
   border-radius: 40px;
   color: #fff;
   font-weight: 400;
   -webkit-transition: 0.2s all;
   transition: 0.2s all;
 }
 
 .notfound a:hover {
   background-color: #fff;
   border-color: aqua;
   color: aqua;
 }
 
 @media only screen and (max-width: 480px) {
   .notfound .notfound-404 h1 {
     font-size: 162px;
   }
   .notfound h2 {
     font-size: 26px;
   }
 }
 