@import url(https://fonts.googleapis.com/css2?family=Rajdhani&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#logo {
  height: 90px;
}

#nav-item {
  font-size: 20px;
  font-family: "Rajdhani", sans-serif;
}

.nav-dropdown {
  padding: 0 1rem;
  font-size: 20px;
  font-family: "Rajdhani", sans-serif;
}

.bg-footerColor {
  background-color: rgba(35, 42, 61, 0.945);
}

.container {
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: rgb(255 255 255);
  padding: 1rem;
}

.main-footer{
  position: relative;
  width: 100%;
  bottom: 0;
}

ul{
  list-style-type: none;
}

.card-title {
  position: absolute;
  font-size: 55px;
  text-transform: uppercase;
  top: 20%;
  left: 10%;
  width: 50%;
  font-weight: 500;
  word-spacing: 5px;
}

.button-title {
  position: absolute;
  top: 35%;
  left: 10%;
  transition: all 0.1s;
}

#button-title:hover {
  background-color: rgb(155, 153, 151);
  text-decoration: none;
}

@media screen and (max-width: 1500px) {
  .card-title {
    font-size: 20px;
  }
}

@media screen and (max-width: 1060px) {
  .card-title {
    font-size: 15px;
  }
}

@media screen and (max-width: 750px) {
    .card-title {
      font-size: 14px;
    }
  }

@media screen and (max-width: 1060px) {
    .card-title {
      font-size: 10px;
    }
  }

#ui-segment{
    margin-top:100px;
}

#notfound {
   position: relative;
   height: 100vh;
 }
 
 #notfound .notfound {
   position: absolute;
   left: 50%;
   top: 50%;
   transform: translate(-50%, -50%);
 }
 
 .notfound {
   max-width: 920px;
   width: 100%;
   line-height: 1.4;
   text-align: center;
   padding-left: 15px;
   padding-right: 15px;
 }
 
 .notfound .notfound-404 {
   position: absolute;
   height: 100px;
   top: 0;
   left: 50%;
   transform: translateX(-50%);
   z-index: -1;
 }
 
 .notfound .notfound-404 h1 {
   color: rgb(250, 227, 227);
   font-weight: 900;
   font-size: 276px;
   margin: 0px;
   position: absolute;
   left: 50%;
   top: 50%;
   transform: translate(-50%, -50%);
 }
 
 .notfound h2 {
   font-size: 56px;
   color: black;
   font-weight: 900;
   text-transform: uppercase;
   margin: 0px;
 }
 
 .notfound p {
   font-size: 15px;
   color: rgb(248, 165, 165);
   font-weight: 400;
   text-transform: uppercase;
   margin-top: 15px;
 }
 
 .notfound a {
   font-size: 14px;
   text-decoration: none;
   text-transform: uppercase;
   background: rgb(198, 198, 235);
   display: inline-block;
   padding: 16px 38px;
   border: 2px solid transparent;
   border-radius: 40px;
   color: #fff;
   font-weight: 400;
   transition: 0.2s all;
 }
 
 .notfound a:hover {
   background-color: #fff;
   border-color: aqua;
   color: aqua;
 }
 
 @media only screen and (max-width: 480px) {
   .notfound .notfound-404 h1 {
     font-size: 162px;
   }
   .notfound h2 {
     font-size: 26px;
   }
 }
 
.page-container{
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content-wrap{
  flex: 1 1;
}
